import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import GameRandom from '@/pages/GameRandom'
import Game24 from '@/pages/Game24'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/games/random',
      name: 'GameRandom',
      component: GameRandom
    },
    {
      path: '/games/24',
      name: 'Game24',
      component: Game24
    }
  ]
})



